    display: flex;
    justify-content: center;
    align-items: center;
    gap: 50px;
    font-family: var(--montserat);
    height: 100vh;
    width: 100%;
}

.home-container .triviaDiv {
    background-color: #698269;;
}

.home-container .tenzieDiv {
    background-color: #567189;
}

.home-container .games-container {
    box-shadow: 0px 0px 20px -5px rgb(255, 255, 255);
    cursor: pointer;
    overflow: hidden;
    padding: 10px;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    border-radius: 50%;
    height: 200px;
    width: 200px;
}

.home-container .games-container h2 {
    color: white;
    letter-spacing: 2px;
    font-size: 2.2rem;
    font-weight: 600;
}

.home-container .games-container .trivia {
    background-image: linear-gradient(to top, #d9f8c8, #ade3bb, #8fd9be, #74cdc2, #3cadb3);
}

.home-container .games-container .tenzie {
    background-color: #0C2435;
}

.home-container .games-container .img {
    padding: 3px;
    margin-top: 5px;
    display: flex;
    align-items: center;
    justify-content: center;
    height: 110px;
    width:220px;
}

.home-container .games-container .img img {
    max-height: 110px;
    width: 160px;
}


@media (max-width: 700px) {
    .home-container {
        flex-direction: column;
    }
}